import React from 'react';
import { useSelector } from 'react-redux';
import { Link } from 'react-router-dom';
import { Menu, Badge } from 'antd';
import styled from 'styled-components'

const Title = styled.span`
  margin: 0 15px
`

function LeftMenu(props) {

  const { currentUser } = useSelector(state => state.user)

  return (
    <Menu mode={props.mode}>
      <Menu.Item><Link to='/'>HOME</Link></Menu.Item>
      <Menu.SubMenu title={<Title></Title>}>
        <Menu.Item key="blog" >
          {<Link to='/blog'></Link>}
        </Menu.Item>
        <Menu.Item key="blog/create" disabled={!currentUser?.isAuth} >
          {<Link to='/blog/create'></Link>}
        </Menu.Item>
      </Menu.SubMenu>

      <Menu.SubMenu title={<Title></Title>}>
        <Menu.Item key="movieTrend">
          {<Link to='/movieTrend'></Link>}
        </Menu.Item>
        <Menu.Item key="movieTrend/favorite" disabled={!currentUser?.isAuth} >
          {<Link to='/movieTrend/favorite'></Link>}
        </Menu.Item>
      </Menu.SubMenu>

      <Menu.SubMenu title={<Title></Title>}>
        <Menu.Item key="talkative" >
          {<Link to='/talkative'></Link>}
        </Menu.Item>
      </Menu.SubMenu>

      <Menu.SubMenu
        title={currentUser?.cart?.length
          ? <Badge dot showZero={false} offset={[-13, 0]}>
            <Title></Title>
          </Badge>
          : <Title></Title>}
      >
        <Menu.Item key="jaymall">
          {<Link to='/jaymall'></Link>}
        </Menu.Item>
        <Menu.Item key="cart" disabled={!currentUser?.isAuth} >
          <Link to='/jaymall/cart'>
            <Badge size='small' count={currentUser?.cart?.length} showZero={false} offset={[0, -10]} />
          </Link>
        </Menu.Item>
        <Menu.Item key="product" disabled={!currentUser?.isAuth} >
          {<Link to='/jaymall/upload'></Link>}
        </Menu.Item>
      </Menu.SubMenu>

      <Menu.SubMenu title={<Title></Title>}>
        <Menu.Item key="jaytube" disabled>
          {<Link to='/jaytube'></Link>}
        </Menu.Item>
      </Menu.SubMenu>

      <Menu.SubMenu title={<Title></Title>}>
        <Menu.Item key="jaytweet" disabled>
          {<Link to='/jaytweet'></Link>}
        </Menu.Item>
      </Menu.SubMenu>
    </Menu>
  )
}

export default LeftMenu
